  private selectedGameId?: number | string;
  private selectedDate?: string;
  private selectedRows?: number;
  private selectedRank?: number;
  private gameChange(game: GameData) {
    const id = game.id as number;
    this.selectedGameId = id as number;
    if (this.$route.params.gameId != game.id) {
      const params: any = {
        gameId: game.id
      }
      this.$router.push({name: 'killplan', params})
      this.callApi();
    }
  }
  private filterOptionChange(value: {[T: string]: string | number}) {
    this.selectedDate = value.date as string;
    this.selectedRows = value.rows as number;
    this.selectedRank = value.rank as number;
    this.callApi();
  }
