





































import { Component, Vue } from 'vue-property-decorator';
import CommonDatePicker from '@/application/Common/View/CommonDatePicker.vue';
import CommonPickerOne from '@/application/Common/View/CommonPickerOne.vue';
import { GameData } from '@/globalInterface';

@Component({
  components: {
    CommonDatePicker,
    CommonPickerOne,
  }
})
export default class KillPlanTableFilter extends Vue {
  private showpopup?: boolean = false;
  private killCount?: number = 202;
  private selectedDate?: string = '';
  private selectedRank?: number = 1;
  private selectedRows?: number = 10;
  private rowData: GameData[] = Array.from(Array(10), (_, i) => ({id:(i + 1) * 10 , text: `近${(i + 1) * 10}期`}));
  private rankData: GameData[] = [
    {
      id: 0,
      text: "冠军"
    },
    {
      id: 1,
      text: "亚军"
    },
    {
      id: 2,
      text: "第三名"
    },
    {
      id: 3,
      text: "第四名"
    },
    {
      id: 4,
      text: "第五名"
    },
    {
      id: 5,
      text: "第六名"
    },
    {
      id: 6,
      text: "第七名"
    },
    {
      id: 7,
      text: "第八名"
    },
    {
      id: 8,
      text: "第九名"
    },
    {
      id: 9,
      text: "第十名"
    },
    {
      id: 10,
      text: "冠亚和"
    }
  ];
  private togglePopup() {
    this.showpopup = !this.showpopup;
  }
  private rankChange(value: GameData) {
    this.selectedRank = value.id as number;
    this.emitData();
  }
  private dateChange(value: string) {
    this.selectedDate = value;
    this.emitData();
  }
  private rowChange(value: GameData) {
    this.selectedRows = value.id as number;
    this.emitData();
  }
  private emitData() {
    this.$emit('filter-option-change', {
      date: this.selectedDate,
      rows: this.selectedRows,
      rank: this.selectedRank
    })
  }
}
